* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list {
  list-style: none;
}

.country-list > li {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.country-list > li > img {
  margin-right: 10px;
}

.country-list > li > span {
  font-weight: 700;
}

.country-info > h1 {
  display: flex;
  align-items: center;
}

.country-info > h1 > img {
  margin-right: 10px;
}

.country-info span {
  font-weight: 700;
}
